body {
    font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
    line-height: 1.4em;
    background: #f5f5f5;
    color: #4d4d4d;
    margin: 0 auto;
    font-weight: 300;
}

div {
    display: block;
}

li {
    list-style-type: none;
}

.form-container {
    display: -ms-flexbox;
    display: flex;
}

.todo-form .form-control {
    width: 100%;
    height: 45px;
}

.todo-form {
    display: block;
    -ms-flex: 1 1;
        flex: 1 1;
}

.completed {
    text-decoration: line-through;
}

ul.footer-list li {
    display: inline;
    margin-right: 5px;
    padding: 3px;
}

.container {
    background: #fff;
    min-width: 450px;
    max-width: 600px;
    margin: 130px 0 40px 0;
    position: relative;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

.todo-footer {
    text-align: center;
    height: 40px;
    -ms-flex-align: center;
        align-items: center;
}

.todo-footer ul {
    display: block;
    margin: 0;
    padding: 0;
}

.item-list li {
    border-bottom: 1px solid gray;
}

.containter{
    border:1px solid gray;
}
.btn {
    font-family: Arial;
    color: #000000;
    font-size: 14px;
    background: #ffffff;
    padding: 10px 20px 10px 20px;
    border: solid #000000 0px;
    text-decoration: none;
    outline: none;
}

.btn:hover {
    text-decoration: none;
    border: 1px solid gray;
    border-radius: 3px;
}

.item-list {
    margin: 0;
    padding: 0;
}

.item-list li {
    position: relative;
    font-size: 16px;
}

.form-control {
    font-family: Arial;
    color: #000000;
    font-size: 16px;
}

.footer-list li button {
    color: inherit;
    margin: 3px;
    padding: 3px 7px;
    text-decoration: none;
    border: 1px solid transparent;
    border-radius: 3px;
}

.todo-count {
    float: left;
    text-align: left;
    padding:0 5px;
}
.item-list{
    border:1px solid gray;
}
.item-list li {
    text-align: left;
    padding:10px 15px;
}
.item-list item {
    padding:10px 0 0 0;
}
.todo-footer{
    display:-ms-flexbox;
    display:flex;
}


